import React, { useEffect } from "react";
import { useTypedSelector } from "../hooks/useTypedSelector";
import { useActions } from "../hooks/useActions";

const getArrayPagination = (len: number, lenmax: number) => {
	if (len === 0 || lenmax === 0) return [1,2,3,4,5];
	let newArrayPagi = (Array(lenmax).fill(0)).map( (el: any, idx: number) => idx+1);
	return newArrayPagi;
}

export const TodoList = (props: any) => {
	const { todos, isLoading, error, page, limit, file  } = useTypedSelector(state => state.todo);
	const pages: number[] = getArrayPagination(todos.len, todos.lenmax);

	//console.log(todos.len, limit)
	const { fetchListTodos, setTodoPage } = useActions();
	useEffect(() => {
		fetchListTodos(page, limit, file);
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [page]);

	if (isLoading) {
		return (<p>Идет загрузка</p>)
	}

	if(error) {
		return (<p>Произошла ошибка</p>)
	}

	return ( 
	<>
	    <table className="App-table">
	    <thead>
	    	<tr>
	    		<td>Page</td>
	    		<td>id</td>
	    		<td>Text</td>
	    		<td>Code</td>
	    	</tr>
	    </thead>
	    <tbody>
		{todos.arr.map( (todo: any, i: number) => (
			<tr key={`${i}`}>
			    <td>{todo._page}</td>
			    <td>{todo.id}</td>
			    <td>{todo.qText}</td>
			    <td><pre>{todo.qCode}</pre></td>
			</tr>
		))}
		</tbody>
		</table>
		<ul className="App-pagination dpl-flex">
		{pages.map( (p: any) => (
			<li
			  onClick={ () => setTodoPage(p) }
			  className={`App-pagination__li ${p===page ? 'App-pagination__active' : 'App-pagination__page'}`}
			  key={`${p}`}>
			    <span>{p}</span>
			</li>
		))}
		</ul>
	</>
	)
}