export interface TodoState {
	todos: {arr: any[], len: number, lenmax: number};
	isLoading: boolean;
	error: null | string;
	page: number;
	limit: number;
	file: string;
}

export enum TodoActionType {
	FETCH_TODOS = 'FETCH_TODOS',
	FETCH_TODOS_SUCCESS = 'FETCH_TODOS_SUCCESS',
	FETCH_TODOS_ERROR = 'FETCH_TODOS_ERROR',
	SET_TODO_PAGE = 'SET_TODO_PAGE',
	SET_TODO_FILE = 'SET_TODO_FILE',
}

interface FetchTodoAction {
	type: TodoActionType.FETCH_TODOS;
}

interface FetchTodoSuccessAction {
	type: TodoActionType.FETCH_TODOS_SUCCESS;
	payload: {arr: any[], len: number, lenmax: number};
}

interface FetchTodoErrorAction {
	type: TodoActionType.FETCH_TODOS_ERROR;
	payload: string;
}

interface SetTodoPage {
	type: TodoActionType.SET_TODO_PAGE;
	payload: number;
}

interface SetTodoFile {
	type: TodoActionType.SET_TODO_FILE;
	payload: string;
}

export type TodoActions = FetchTodoAction | FetchTodoSuccessAction | FetchTodoErrorAction | SetTodoPage | SetTodoFile;