import axios from "axios";
import { Dispatch } from "redux";
import { TodoActions, TodoActionType } from "../../types/todo";

interface JsonData {
  qText: string;
  qCode: string;
  answers: string[];
  hint: string | string[];
  typos: string;
  id: number;
}

const getAnyValue = (data: any[]) => {
	return Math.max.apply(Math, data);
}

const _uniqArray = (unit: number, start: number = 0, end: number = 8) =>  {
	const nums = Array(unit).fill('').map( (_, index) => index + 1);
	nums.sort(() => Math.random() - 0.5);
	return nums.slice(start, end);
}

const getUniqueData = (array: JsonData[]): JsonData[] => {
	const ua = _uniqArray(array.length, 0, 5);
	const getUniqData = array.filter( (el: any, idx: number) => {
	    return ua.includes(idx+1) && el
	});
	return getUniqData;
}

export const fetchTodos = (page: number, limit: number, file: string)  => {
	return async (dispatch: Dispatch<TodoActions>) => {
		try{
			dispatch({ type: TodoActionType.FETCH_TODOS, });
			const res = await axios.get(`./json/${file}.json`,
				{
					//params: {_page: page, _limit: limit}
				});
			const resGo = getUniqueData(res.data);
			const listLength = res.data.length;
			const pageMaxArray = res.data.map( (p:any) => p._page);
			const pageMaxValue = getAnyValue(pageMaxArray);

			setTimeout( () => {
				dispatch({ 
					type: TodoActionType.FETCH_TODOS_SUCCESS, 
					payload: {arr: resGo, len: listLength, lenmax: pageMaxValue}, 
				});
			}, 0)
		}
		catch (e){
			dispatch({ 
				type: TodoActionType.FETCH_TODOS_ERROR, 
				payload: 'Произошла ошибка при загрузке Todo',
			});
		}
	}
}

export const fetchListTodos = (page: number, limit: number, file: string)  => {
	return async (dispatch: Dispatch<TodoActions>) => {
		try{
			dispatch({ type: TodoActionType.FETCH_TODOS, });
			const res = await axios.get(`./json/${file}.json`,
				{
					//params: {_page: page, _limit: limit}
				});
			const resGo = res.data.filter( (p: any, idx: number) => p._page === page && idx <= limit);
			const listLength = res.data.length;
			const pageMaxArray = res.data.map( (p:any) => p._page);
			const pageMaxValue = getAnyValue(pageMaxArray);

			setTimeout( () => {
				dispatch({ 
					type: TodoActionType.FETCH_TODOS_SUCCESS, 
					payload: {arr: resGo, len: listLength, lenmax: pageMaxValue}, 
				});
			}, 0)
		}
		catch (e){
			dispatch({ 
				type: TodoActionType.FETCH_TODOS_ERROR, 
				payload: 'Произошла ошибка при загрузке Todo',
			});
		}
	}
}

export function setTodoPage(page: number): TodoActions {
	return {
		type: TodoActionType.SET_TODO_PAGE,
		payload: page,
	}
}

export function setTodoFile(file: string): TodoActions {
	return {
		type: TodoActionType.SET_TODO_FILE,
		payload: file,
	}
}