//export const FETCH_DATA = 'FETCH_DATA';

export interface HistoryState {
	dataQuiz: any[];
	isLoading: boolean;
	error: null | string;
}

export enum HistoryActionType {
	FETCH_DATA = 'FETCH_DATA',
	FETCH_DATA_SUCCESS = 'FETCH_DATA_SUCCESS',
	FETCH_DATA_ERROR = 'FETCH_DATA_ERROR',
}

interface FetchHistoryAction {
	//type: typeof FETCH_DATA;
	type: HistoryActionType.FETCH_DATA,
}

interface FetchHistorySuccessAction {
	type: HistoryActionType.FETCH_DATA_SUCCESS;
	payload: any[];
}

interface FetchHistoryErrorAction {
	type: HistoryActionType.FETCH_DATA_ERROR;
	payload: string;
}

export type HistoryAction = FetchHistoryAction | FetchHistorySuccessAction | FetchHistoryErrorAction;