import axios from "axios";
import { Dispatch } from "redux";
import { HistoryAction, HistoryActionType } from "../../types/data";


export const fetchDatas = ()  => {
	return async (dispatch: Dispatch<HistoryAction>) => {
		try{
			dispatch({ type: HistoryActionType.FETCH_DATA, });
			const res = await axios.get('./json/list0.json');
			setTimeout( () => {
				dispatch({ 
					type: HistoryActionType.FETCH_DATA_SUCCESS, 
					payload: res.data, 
				});
			}, 500)
		}
		catch (e){
			dispatch({ 
				type: HistoryActionType.FETCH_DATA_ERROR, 
				payload: 'Произошла ошибка при загрузке вопросов',
			});
		}
	}
}