import { HistoryAction, HistoryActionType, HistoryState } from "../../types/data";

const initialState: HistoryState  = {
	dataQuiz: [],
	isLoading: false,
	error: null,
}

export const historyReducer = (state = initialState, action: HistoryAction): HistoryState => {
	switch(action.type){
		case HistoryActionType.FETCH_DATA: 
		    return { dataQuiz: [], isLoading: true, error: null }
		case HistoryActionType.FETCH_DATA_SUCCESS:
		    return { dataQuiz: action.payload, isLoading: false, error: null }
		case HistoryActionType.FETCH_DATA_ERROR:
		    return { dataQuiz: [], isLoading: false, error: action.payload }
		default:
		    return state
	}
}