function isActive(hint: any, item: any) {
    if ( Array.isArray(hint) ) {
        return hint.includes(item);
    }
    let reg = new RegExp(`(${item})`, ``);
    //console.log( hint === item, reg, ', search: ', hint.search(reg) !== -1);
    //console.log( hint === item, reg, ', test: ', hint.match(reg) );
    return hint.search(reg) !== -1 && hint===item ? true : false;
}

export function Label(props: any){
	const { go, idx, onChange, check } = props;
    const x: any = check[idx];
    //console.log('x: ', x);
    return(
    	<>
        {go.answers.map( (item: string[], index: number) => (
            <label 
                htmlFor={`id_${idx}_${index}`}
                key={`label_key_${index}`}
                className={`App-form__label ${isActive(go.hint, item) ? 'active' : ''}`}
            >
                <input 
                    id={`id_${idx}_${index}`}
                    className="App-form__input"
                    name={idx}
                    type={go.typos}
                    value={item}
                    onChange={ (e) => onChange(e) }
                    //checked={Array.isArray(x) ? x[index] : (item === x) ? true : false}
                />
                {item}
            </label>
        ))}
        </>
    );
}