import React from "react";
import { useActions } from "../hooks/useActions";
import { useTypedSelector } from "../hooks/useTypedSelector";
import { Label } from "./Label";

interface LocalStore {
  totalQuetions: number;
  rightAnswers: number;
  levelOfKnown: number;
}

interface JsonData {
  qText: string;
  qCode: string;
  answers: string[];
  hint: string | string[];
  typos: string;
  id: number;
}

const saveHistoryData = (): any[] => {
	if (!localStorage['jsquiz']) localStorage['jsquiz'] = '[]';
	return JSON.parse( localStorage['jsquiz'] );
}

const linkHistory = (quizLength: number, rightAnswersLength: number, knowPercentage: number) => {
	let stored: LocalStore = {
	  "totalQuetions": quizLength,
	  "rightAnswers": rightAnswersLength,
	  "levelOfKnown": knowPercentage,
	}
	let arr: any[] = saveHistoryData();
	arr.push( stored );
	let newArr: string = JSON.stringify( arr );
	localStorage['jsquiz'] = newArr;
}

export const QuizHook = (props: any) => {
	const { todos, isLoading, error, page, limit, file  } = useTypedSelector(state => state.todo);
	const { fetchTodos } = useActions();
	React.useEffect(() => {
		fetchTodos(page, limit, file);
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, []);

	const [activeItem, setActiveItem] = React.useState(false);
	const [isGoing, setGoing] = React.useState(false);
	const [quizItem, setQuiz] = React.useState(Array(todos.arr.length).fill(null));
	const [totalRightAnswer, setTotalRightAnswer] = React.useState(0);
	const [totalPercentage, setTotalPercentage] = React.useState(0);
	const [isChecked, setIsChecked] = React.useState(Array(todos.arr.length).fill(false));

	React.useEffect( () => {
		setActiveItem(false);
		setGoing(false);
		setQuiz( Array(todos.arr.length).fill(null) );
		setTotalRightAnswer(0);
		setTotalPercentage(0);
		setIsChecked( Array(todos.arr.length).fill(false) );
		//linkHistory(quizItem.length, totalRightAnswer, totalPercentage);
		checkOutInput();
	}, [todos.arr]);


	const setCheckArray: any = (cur: any, defValue: null | boolean, action: string) => {
		let newArray: any = [];
		[].forEach.call(cur.children, (el: any, idx: number) => {
			let input = el.children[0].checked;
			//console.log('checkbox - input: ', input);

			if( input === true ){
			    newArray[idx] = el.children[0][action]
			} else {
			    newArray[idx] = defValue;
			}
		});

		if( newArray.filter((el: any) => el===defValue).length === cur.children.length ) {
		    return newArray = defValue;
		}
		return newArray;
	}

	const onCheckedAnswer: any = (event: any) => {
    const target = event.target;
    const index = Number(target.name);
    let curTarget = event.currentTarget.parentNode.parentNode;
    const value = target.type === 'radio' ? target.value : setCheckArray(curTarget, null, 'value');
    const checker = target.type === 'radio' ? target.value : setCheckArray(curTarget, false, 'checked');

    let newQuiz = quizItem;
    let newChecked = isChecked;
    newQuiz[index] = value;
    newChecked[index] = checker;

    let isGo: boolean = !newQuiz.includes(null);
    setQuiz( newQuiz );
    setIsChecked( newChecked );
    setGoing(isGo);
    //console.log('On check answer!', newQuiz, newChecked, isGo, curTarget)
	}

	const handleSubmit = (event: any) => {
		let quizGo = quizItem;

	    if (quizGo.length < 1 || quizGo.length !== todos.arr.length) {
	      return event.preventDefault();
	    }

	    if( quizGo.length === todos.arr.length && !quizGo.includes(null) ){
	      _sumArray(quizGo);
	      // setTimeout( () => {
	      //   linkHistory(quizItem.length, totalRightAnswer, totalPercentage);
	      // }, 0);
	    }

	    event.preventDefault();
	}

	const _sumArray = (quiz: any[]) => {
	    let resArr: number[] = [];
	    let hintArr: any[] = [];

	    todos.arr.forEach( (item: any ) => {
	      hintArr.push(item.hint);
	    });

	    quiz.forEach( (elem, index) => {
	      if( typeof elem === 'string' ){
	        (elem.includes(hintArr[index]) && elem ===  hintArr[index]) ? resArr.push(1) : resArr.push(0);
	      } else {
	        resArr.push(_sumToAnswer(hintArr[index], elem));
	      }
	    });

		toggleActiveItem();
		setGoing(false);

	    let summary = resArr.reduce( (s, el) => {
	      return s + el
	    }, 0);
	    let totalResult = Math.round(summary / quiz.length * 100);
	    let totalAns = resArr.filter(el => el>0).length;


	    setTotalPercentage( totalResult );
	    setTotalRightAnswer( totalAns );

	    linkHistory(quizItem.length, totalAns, totalResult);
	    return resArr;
	}

	const _sumToAnswer = (hint: string[], quiz: any[]) => {
		const koef = (1 / hint.length);
		let rightAnswer: number = 0;

		quiz.forEach( (num) => {
		  hint.forEach( (h) => {
		    h.includes(num) ? rightAnswer += koef : rightAnswer += 0;
		  });
		});
		return rightAnswer;
	}

	const toggleActiveItem = () => {
		setActiveItem(() => !activeItem);
	}

	const checkOutInput = () => {
		Array.from(document.querySelectorAll('input:checked')).find( (el: any) => {
			return el.checked = false
		});
	}

	const loadQuiz = () => {
		fetchTodos(page, limit, file);
		linkHistory(quizItem.length, totalRightAnswer, totalPercentage);
	}

	if (isLoading) {
		return (<p>Идет загрузка</p>)
	}

	if(error) {
		return (<p>Произошла ошибка</p>)
	}

	return (
		<div>
			<h4 className="App-reader">Read the questions carefully:</h4>
			<form 
			    onSubmit={(e) => handleSubmit(e)}
			    className={`App-form ${activeItem ? 'showItem' : ''}`}
			    id="blya">
				<ul>
					{todos.arr && todos.arr.map( (el: JsonData, i: number) => (
					<li key={i} className={`App-list__li`}>
            <div className={`App-form__title`}>
              <h4 className="App-code__text">{el.id} - {el.qText}</h4>
              <pre className="App-code__box">{el.qCode}</pre>
            </div>
            <div data-index={i.toString()} className="App-form__item">
              <Label
                key={`label_${i}`} 
                go={el} 
                idx={i.toString()}
                check={isChecked}
                onChange={ onCheckedAnswer } 
                />
            </div>
					</li>
					))}
				</ul>
				<input
					type="submit"
					disabled={!isGoing}
					value="Summary" />
				<button onClick={loadQuiz}>Get next quiz</button>
			</form>
	        <div className={`App-form__result`}>
	          <p>Total right answers: {totalRightAnswer} from {quizItem.length} questions</p>
	          <p>Your knowledge of js: {totalPercentage} %</p>
	        </div>
		</div>
	)
}

export default QuizHook;