import React, { useEffect } from "react";
import { useTypedSelector } from "../hooks/useTypedSelector";
import { useActions } from "../hooks/useActions";

const History = (props: any) => {
	const { isLoading, error } = useTypedSelector(state => state.data);
	const { fetchDatas } = useActions();
	useEffect(() => {
		fetchDatas();
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, []);

	//console.log('dataQuiz: ', dataQuiz);

	if (isLoading) {
		return (<div>Идет загрузка...</div>);
	}

	if(error) {
		return (<div>Произошла ошибка</div>);
	}

	const xyz: any[] = !localStorage['jsquiz'] ? 'Empty' : JSON.parse(localStorage['jsquiz']);
	return (
		<>
		    <h2>List of your results is</h2>
		    {Array.isArray(xyz) ? (<table className="App-table">
		        <thead>
		            <tr>
		                <td>Shot</td>
		                <td>Total Questions</td>
		                <td>Right Answers</td>
		                <td>Level of Knows (%)</td>
		            </tr>
		        </thead>
		        <tbody>
		    	{ (xyz) ? xyz.map( (el, idx) => (
		    		<tr key={`quiz_${idx}`}>
		    		    <td>{idx+1}</td>
		    		    <td>{el.totalQuetions}</td>
		    		    <td>{el.rightAnswers}</td>
		    		    <td>{el.levelOfKnown} %</td>
		    		</tr>
		    	)) : (<tr><td>???</td></tr>)}
		    	</tbody>
		    </table>) : (<p>Empty</p>)}
		</>
	);
};

export { History };