import React from "react";
import { useActions } from "../hooks/useActions";
import { useTypedSelector } from "../hooks/useTypedSelector";

export const SelectFile = () => {
	const { error, page, limit  } = useTypedSelector(state => state.todo);
	const [file, setFile] = React.useState('list0');
	const [selectValue, setSelectValue] = React.useState('list0');
	const { fetchTodos, setTodoFile, setTodoPage } = useActions();

	React.useEffect(() => {
		fetchTodos(page, limit, file);
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [file]);

	const OnSelectFile = (event: any) => {
		let selFile = event.target.value;
		if( selFile !== file ) setFile(selFile);
		
		setTodoFile(selFile);
		setSelectValue(selFile);
		setTodoPage(1);
	}

	if(error) {
		return (<p>Произошла ошибка</p>)
	}

	return (
		<select value={selectValue} onChange={(e) => OnSelectFile(e)}>
			<option value="list0">List 0</option>
			<option value="list1">List 1</option>
			<option value="list2">List 2</option>
		</select>
	);
}